<template>

  <div class="consultaTramite">
    <div class="container">
      <div class="card card-muni">
        <div class="filtro">
          <div class="row">
            <div class="col-md-8">
              <select id="ddlTipoDocumento" class="form-control" v-model="idTipoTramite">
                <option value="0">Todos</option>
                <option v-for="tipoTramite of listaTramite" :key=tipoTramite.idTipoTramite :value="tipoTramite.idTipoTramite" >{{tipoTramite.nombre}}</option>
              </select>
            </div>
            <div class="col-md-2">
              <select class="form-control" v-model="idEstado">
                <option value="0">Todos</option>
                <option v-for="estado of listaEstado" :key=estado.idParametro :value="estado.idParametro" >{{estado.nombre}}</option>
              </select>
            </div>
            <div class="col-md-2">
              <button type="button" @click="getTramites(1)" class="btn btn-default btn-block btn-lg">Buscar</button>   
            </div>
            
          </div>
        </div>
        <div class="lista">
          <table class="table table-hover">
            <thead>
              <tr >
              <th width="5%">Solicitud</th>
              <th width="5%">Documento</th>
              <th width="25%">Tipo Trámite</th>
              <th width="25%">Fecha Creacion</th>
              <th width="7%">Estado</th>
            </tr>
            </thead>
            <tbody>
             <!-- <tr v-for="tramite of tramitesJson" :key=tramite.idTramite v-on:dblclick="Editar(tramite.unidadDestino.uniorgcod,tramite.tipoTramite.idTipoTramite, tramite.idTramite)"> -->
                <tr v-for="tramite of tramitesJson" :key=tramite.idTramite @dblclick="redirecionar(tramite)">
                <td v-if="tramite.idTramite>0" >ST-00{{tramite.idTramite}}</td>
                <td v-else></td>
                <td v-if="tramite.idTramite>0 && tramite.id011Estado.idParametro==24">
                {{tramite.tipoTramite.id008Tipo.abreviatura}}-{{tramite.expedientePosgre.numeroExpediente}}-{{(tramite.fechaAprobacion).substr(0,4)}} 
                </td>
                <td v-else-if="tramite.idTramite==0">{{tramite.expedientePosgre.numeroExpediente}}</td>
                <td v-else></td>
                <!-- <td v-if="idTipoBusquedaFiltro==2"><a href="javascript:;" @click="Detalle(tramite.expedientePosgre.idExpedienteOracle.ideExpediente)" >{{tramite.expedientePosgre.numeroExpediente}}</a></td>
                <td v-else-if="tramite.id011Estado.idParametro==24"> <a href="javascript:;" @click="Detalle(tramite.expedientePosgre.idExpedienteOracle.ideExpediente)" >{{tramite.tipoTramite.id008Tipo.abreviatura}}-{{tramite.expedientePosgre.numeroExpediente}}-{{(tramite.fechaAprobacion).substr(0,4)}} </a></td>
                <td v-else></td>  -->
                <!-- <td>ST-00{{tramite.idTramite}}</td> -->
                <td>{{tramite.tipoTramite.nombre}}</td>
                <td>{{tramite.fechaCreacion}}</td>
                <td v-if="idTipoBusquedaFiltro==1 && tramite.id011Estado.idParametro==27" class="bg-danger-new" >
                  <span>{{tramite.id011Estado.nombre}}</span>
                </td>
                <td v-else >{{tramite.id011Estado.nombre}}</td>
              </tr>
            </tbody>
          </table>
          <nav aria-label="..." class="aling-right">
            <ul class="pagination justify-content-center">
              <li class="page-item " @click="CambiarPagina(indice-1)">
                <span class="page-link">Anterior</span>
              </li>
              <li v-for="pagina of listaPaginas" :key=pagina.id @click="CambiarPagina(pagina)" class="page-item" :class="{'active':pagina==indice}">
                <a class="page-link" >{{pagina | pintaPuntos}}</a>
              </li>
              <li class="page-item" @click="CambiarPagina(indice+1)">
                <a class="page-link" >Siguiente</a>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </div>
  </div>

</template>
<script>
import axios from 'axios';
import Constantes from '../../store/constantes.js';
import EventBus from "../../helpers/EventBus.js"
export default {
  name:'BandejaCertificado',
  data(){
    return{
      certificadosJson:null,
      tramitesJson:null,
      listaParametrosAux:null,
      listaEstado:null,
      listaTipoDocumento:null,
      listaTramite:null,
      idTipodocumento:0,
      idTipoTramite:0,
      idEstado:0,
      numeroDocumentoModelo:'',
      numeroExpedienteModelo:'',
      anioExpedienteModelo:'',
      manzanaModelo:'',
      loteModelo:'',
      indice:1,
      limite:10,
      listaPaginas:null,
      cantidadPaginasBandeja:0
    }
  },
  created(){
    this.idTipoBusqueda=1;
    this.idTipoBusquedaFiltro=1;
    this.getParametros(11);
    //this.getCertificados();
    this.getParametros(4);
    this.getTramites(1);
    this.getIdTipoTramite();
    
  },
  methods:{
    redirecionar(tramite){
      if(tramite.idTramite>0 && tramite.id011Estado.idParametro==24){
        this.Detalle(tramite.expedientePosgre.idExpedienteOracle.ideExpediente);
      }else if(tramite.idTramite>0 ){
        this.Editar(tramite.unidadDestino.uniorgcod,tramite.tipoTramite.idTipoTramite, tramite.idTramite)
      }
    },
    CambiarPagina(pagina){

      if(pagina>0){
        this.indice=pagina;
        this.getTramites(this.indice);
      }
    },
    getIdTipoTramite(){
      axios.get(Constantes.rutaTramite+'tipotramiteUsuario/-/'+localStorage.getItem('idUsuarioLogueadoTramite'))
                    .then(response=>{
                    this.listaTramite=response.data.data;
                    
                  })
                  .catch(e=>console.log(e))
    },
    getParametros(grupo){
      axios.get(Constantes.rutaTramite+'parametro/'+grupo+'/0')
                    .then(response=>{
                    switch(grupo){
                      case 4: 
                        {
                          // console.log('examinando listaTipoDocumento');
                          this.listaTipoDocumento=response.data.data;
                          // console.log(this.listaTipoDocumento);
                        };
                        break;
                      case 11: 
                        {
                          // console.log('examinando listaEstado'+grupo);
                          this.listaEstado=response.data.data;
                          this.idEstado=0;
                        };
                        break;
                    }

                  })
                  .catch(e=>console.log(e))
    },
    reemplazaVacio(cadena,reemplazo){
      return cadena==''?'-':cadena;
    },
    reemplazaVacioEntero(cadena,reemplazo){
      return cadena==''?'0':cadena;
    },
    getTramites(pagina){
        // EventBus.$emit("loadershow", true);
        //var url=Constantes.rutaTramite+'bandeja-tramites-usuario/0/'+this.idEstado+'/'+this.idTipoTramite + '/' + this.reemplazaVacio(this.numeroDocumentoModelo)+'/0/'+this.indice+'/'+this.limite+'/'+localStorage.getItem('idUsuarioLogueado');
        this.indice=pagina;
        var url="";
           url=Constantes.rutaTramite+'bandeja-tramites-usuario/0/'+this.idEstado+'/'+this.idTipoTramite + '/' + this.reemplazaVacio(this.numeroDocumentoModelo)+'/0/'+pagina+'/'+this.limite+'/'+localStorage.getItem('idUsuarioLogueadoTramite')+'/'+localStorage.getItem('idPersonaLogueadoTramite');
        
        
        // console.log('url tramites get:'+ url);
                axios.get(url)
                    .then(response=>{
                    // console.log(response);
                    this.tramitesJson=response.data.data;
                    var objeto=this.tramitesJson.data;
                    // console.log('cantidad de tramites'+ this.tramitesJson.length);
                    this.listaPaginas=null
                    this.listaPaginas=[1];
                    var cantidadPaginas=0;
                    this.cantidadPaginasBandeja=0;
                    // console.log('cantidad de páginas'+ cantidadPaginas);
                    if(this.tramitesJson.length>0){
                      
                      cantidadPaginas=Math.ceil(this.tramitesJson[0].totalBandeja/this.limite);
                      this.cantidadPaginasBandeja=cantidadPaginas;
                      // console.log('cantidad de páginas'+ cantidadPaginas);
                       var nuevaPagina=2;
                       var primeraPaginaAuxiliar=0;
                       var segundaPaginaAuxiliar=0;
                       if(this.cantidadPaginasBandeja<=10){
                          while(nuevaPagina<=cantidadPaginas) {
                              this.listaPaginas.push(nuevaPagina);
                              nuevaPagina++
                              // console.log('Ingreso a pushear listaPaginas' + nuevaPagina);
                              // console.log(this.listaPaginas);
                          }
                       }else{
                          while(nuevaPagina<=cantidadPaginas) {
                              // console.log('pintando indice'+this.indice);
                              if(nuevaPagina<=3){
                                this.listaPaginas.push(nuevaPagina);
                                // console.log('Ingreso a pushear listaPaginas' + nuevaPagina);
                                // console.log(this.listaPaginas);
                              }else{
                                if((nuevaPagina>this.indice-3 && nuevaPagina<this.indice+3)||nuevaPagina>=this.cantidadPaginasBandeja-3){
                                  this.listaPaginas.push(nuevaPagina);
                                  // console.log('Ingreso a pushear listaPaginas' + nuevaPagina);
                                  // console.log(this.listaPaginas);
                                }else{
                                  if(primeraPaginaAuxiliar==0 && nuevaPagina<=this.indice-3){
                                    this.listaPaginas.push(-1);
                                    primeraPaginaAuxiliar=1;
                                  }
                                  if(segundaPaginaAuxiliar==0 && nuevaPagina>=this.indice+3){
                                    this.listaPaginas.push(-1);
                                    segundaPaginaAuxiliar=1;
                                  }
                                }
                              }
                              
                              nuevaPagina++;
                          }
                       }
                       
                    }
                    // EventBus.$emit("loadershow", false)
                  })
                  .catch(e=>console.log(e))
    },
    Editar(idArea,idTipoTramite,idTramite){
                /* this.$router.push('tramite/observado/'+idArea+'&'+idTipoTramite+'&'+idTramite) */
                this.$router.push('tramite/ce/'+idArea+'&'+idTipoTramite+'&'+idTramite+'&0')
            },
    PadLeft(value, length) {
            return (value.toString().length < length) ? PadLeft("0" + value, length) : value;
            },
    Detalle(idTramite){
          this.$router.push('detalle/'+idTramite)
    }

  },
  filters:{
    pintaPuntos(pagina){
      if(pagina==-1){
        return '...';
      }
      else{
        return pagina;
      }
    },
    upper: function (value) {
      return value.toLowerCase();
    }
  }
}
</script>
<style lang="scss" scoped>
  .consultaTramite {
    padding-bottom: 50px;
    .filtro {
      padding-bottom: 20px;
      .form-control {
        padding-right: 45px;
      }
    }
    .pagination {
      padding-bottom: 30px;
      padding-top: 30px;
    }
  }
  table td {
    text-transform: capitalize;
    cursor: pointer;
    vertical-align: middle;
  }
  .table thead th {
    border-bottom: 0;
  }
</style>


